<template>
	<div>
		<header>
			<router-link to='/kstfb' class="goback"></router-link>
			<p>阅读训练1</p>
		</header>
		<div class="info">
			<div class="i">
				<span class="icon"></span>
				<p class="title">完成人数</p>
				<p class="data"><span>13</span>/50</p>
			</div>
			<div class="i">
				<span class="icon"></span>
				<p class="title">完成率</p>
				<p class="data">30%</p>
			</div>
			<div class="i">
