					<p>{{d.title}}</p>
				</div>
			</div>
		</div>
		<div class="footer">
			<div class="gk">
				<p>概括</p>
			</div>
			<router-link to='/kstfb/bgxq' class="xq">详情
			</router-link>
		</div>
	</div>
</template>

<script>
	import probar from './pro_bar.vue'
	export default {
		data(){
			return {
				// 进度条数据
				data: [
					{id: 1,data: 30, color: '#ffc474', title: '提取和推论'},
					{id: 1,data: 80, color: '#f66c6c', title: '形成解释'},
					{id: 1,data: 100, color: '#79a8ec', title: '做出评价'},
					{id: 1,data: 50, color: '#40cc8b', title: '创意运用'},
