<template>
	<div>
		<header>
			<router-link to='/main' class="goback"></router-link>
			<p>我的收藏</p>
		</header>
		<!-- <div class="search">
			<input type="text" name="" placeholder="搜索">
		</div> -->
		<div class="content" v-for='(l, i) in list'>
			<div class="c_m" v-touch>
				<div class="info">
					<img :src="l.tx">
					<p class="name">{{l.name}}</p>
					<p class="time">{{l.time}}</p>
				</div>
				<div class="cnt">
					<img :src="l.src">
					<p>{{l.cnt}}</p>
				</div>
			</div>
			<button @click='del(i)' v-init></button>
		</div>
		<tips :msg='message' v-show='t_flag'></tips>
	</div>
</template>

<script>
	var timer=null;
	import tips from './msgtips.vue'
	export default {
		data(){
			return {
				// 提示框
				message: '',
				t_flag: false,
				list: [],
				// 页码
				page: 1,
				// 上拉加载
				sl_flag: false,
				nextpage: true
			}
		},
		methods: {
			del: function(i){
				// 删除
				this.list.splice(i, 1);
			},
			_load: function(){
				var self=this;
				// 请求下载资源
				this.util.request(
				{
					url: self.globalurl.host+self.globalurl.url.wdsc,
					data: {"loginAccount":self.util.getSessionData("tId"), "page": self.page, "pageSize": 10},
					dataType: 'json',
					type: 'POST'
				}, function(data){
					console.log(data);
					if(data.status==200&&data.result!=null){
						for(let i=0;i<data.result.length;i++){
							self.list.push(data.downloads[i]);
						}
						self.t_flag=false;
						self.nextpage=data.nextPage;
					}
					if(data.status==200&&data.result.length==0){
						clearTimeout(timer);
						timer=null;
						self.message='暂无数据';
						self.t_flag=true;
						timer=setTimeout(function(){
							self.t_flag=false;
						}, 2000);
					}
					if(data.status==404){
						clearTimeout(timer);
						self.message='暂无数据';
						timer=setTimeout(function(){
							self.t_flag=false;
						}, 2000);
					}
				}, self)
			}
		},
		created: function(){
			var self=this;
			this.message='加载中';
			this.t_flag=true;
			// 请求下载资源
			this._load();

			// 上拉加载
			window.addEventListener('scroll', function(){
				if(document.body.scrollTop+document.documentElement.clientHeight==document.body.scrollHeight){
					if(self.$route.path=='/main/sc'){
						if(self.nextpage){
							self.page+=1;
							self.sl_flag=true;
							self._load();
						}else{
							clearTimeout(timer);
							timer=null;
							self.message='数据已到底';
							self.t_flag=true;
							timer=setTimeout(function(){
								self.t_flag=false;
							}, 2000);
						}
					}
				}
			})
		},
		directives: {
			// 左拉删除
			touch: {
				inserted: function(el){
					// 手指初始位置
					var star=null;
					// 手指结束位置
					var end=null;
					// 偏移量
					var left=null;
					el.addEventListener('touchstart', function(event) {
							// 初始化偏移量
							var content=el.parentNode.parentNode.getElementsByTagName('div');
							console.log(content);
							for(let i=0;i<content.length;i++){
								if(content[i].className=='content'){
									console.log(content[i]);
									content[i].getElementsByTagName('div')[0].style.left='0px';
								}
							}
						     // 如果这个元素的位置内只有一个手指的话
						     console.log(event.targetTouches.length);
						    if (event.targetTouches.length == 1) {// 阻止浏览器默认事件，重要
						　　　　 event.preventDefault();
						        var touch = event.targetTouches[0];
						        // 把元素放在手指所在的位置
						    	star=touch.clientX;
							console.log(star);
						        }
					}, false);
					el.addEventListener('touchmove', function(event) {
						     // 如果这个元素的位置内只有一个手指的话
						     console.log(event.targetTouches.length);
						    if (event.targetTouches.length == 1) {
						　　　event.preventDefault();// 阻止浏览器默认事件，重要 
						        var touch = event.targetTouches[0];
						        // 把元素放在手指所在的位置
						    	end=touch.clientX;
							console.log(end);
							left=end-star;
							console.log(left);
							if(left>0){
								el.style.left="0px";
							}else if(-left>100){
								el.style.left='-2rem';
							}else{
								el.style.left=left+'px';
						          }
						}
					}, false);
					el.addEventListener('touchend', function(event) {
						if(left>-100){
							el.style.left="0px";
						}
					}, false);
				}
			},
			// 删除后恢复原位
			init: {
				inserted: function(el){
					el.onclick=function(){
						// 初始化偏移量
						this.parentNode.getElementsByTagName('div')[0].style.left='0px';
					}
				}
			}
		},
		components: {
			'tips': tips
		}
	}
</script>

<style scoped>
	header{
		background-color: #fff;
		border-bottom: 0.01rem solid #e7e7e7;
	}
	.goback{
		background-image: url('../../static/images/pic_fh01.png');
	}
	header p{
		color: #333333;
		font-weight: 500;
	}
	.search{
		margin-top: 0.88rem;
		padding: 0.11rem 0.3rem;
		width: 100%;
	}
	.search input{
		display: block;
		border-radius: 0.1rem;
		width: 100%;
		height: 0.57rem;
		border: 0.01rem solid #e7e7e7;
		background-color: #fff;
		padding-left: 0.6rem;
		background-image: url('../../static/images/ico_look.png');
		background-size: 0.31rem 0.31rem;
		background-repeat: no-repeat;
		background-position: 0.15rem center;
	}
	.content{
		position: relative;
		margin-bottom: 0.1rem;
		border-top: 0.01rem solid #e7e7e7;
		border-bottom: 0.01rem solid #e7e7e7;
	}
	.content:nth-child(2){
		margin-top: 1rem;
	}
	.c_m{
		position: relative;
		left: 0px;
		padding: 0.3rem;
		background-color: #fff;
		z-index: 2;
	}
	.content button{
		border: 0px;
		background-color: #fff;
		width: 0.9rem;
		height: 0.9rem;
		margin-top: -0.45rem;
		position: absolute;
		top: 50%;
		right: 0.5rem;
		z-index: 1;
		border-radius: 100%;
		background-image: url('../../static/images/delete.png');
		background-size: 100% 100%;
		border-radius: 100%;
	}
	.info{
		width: 100%;
		overflow: hidden;
		font-size: 0.24rem;
	}
	.info img{
		float: left;
		width: 0.6rem;
		height: 0.6rem;
		border-radius: 100%;
	}
	.name{
		line-height: 0.6rem;
		color: #44ce8e;
		float: left;
		margin-left: 0.2rem;
	}
	.time{
		line-height: 0.6rem;
		float: right;
		color: #999999;
	}
	.cnt{
		margin-top: 0.2rem;
		width: 100%;
		padding: 0.1rem;
		overflow: hidden;
		background-color: #f7f7f7;
	}
	.cnt img{
		float: left;
		height: 1rem;
		margin-right: 0.26rem;
	}
	.cnt p{
		font-size: 0.26rem;
		color: #333333;
		line-height: 0.4rem;
	}
</style>


