<template>
	<div>
		<header>
			<p>我的</p>
		</header>
		<div class="top">
			<div class="info">
				<div class="img"><img :src="avatar"></div>
				<p class="name">{{name}}&nbsp;&nbsp;&nbsp;老师</p>
				<p class="school">{{schoolName}}</p>
			</div>
			<div class="teachinfo">
				<div class="t_i">
					<router-link to='/main/rw'>
						<p>{{tasks}}个</p>
						<p>发布任务量</p>
					</router-link>
				</div>
				<div class="t_i">
					<router-link to='/main/dhg'>
						<p>{{reaction}}份</p>
						<p>批阅读后感</p>
					</router-link>
				</div>
				<div class="t_i">
					<p>{{score}}分</p>
					<p>积分</p>
				</div>
				<!-- 签到 -->
				<!-- <div class="qd"><a href="#"></a></div> -->
			</div>
		</div>
		<div class="main">
			<router-link to='/main/sc'><span class="groud"></span>我的收藏<span class="count zore">0</span></router-link>
			<!-- <router-link to='/main/fx'><span class="groud"></span>我的分享<span class="count">3</span></router-link> -->
			<router-link to='/main/xz'><span class="groud"></span>我的下载<span class="count">{{download}}</span></router-link>
			<!-- <router-link to='/'><span class="groud"></span>我参加的活动<span class="count">2</span></router-link> -->
			<router-link to='/main/xgxx'><span class="groud"></span>设置</router-link>
		</div>
		<button class="quit" @click="_exit">更改登录账号</button>
		<foot :show="2"></foot>
		<tips :msg='message' v-show='t_flag'></tips>
	</div>
</template>

<script>
	var timer=null;
	import tips from './msgtips.vue'
	import f from './footer.vue'
	export default {
		data(){
			return {
				// 名字
				name: '',
				// 头像
				avatar: '',
				// 学校
				schoolName: '',
				// 任务个数
				tasks: 0,
				// 积分
				score: 0,
				// 下载个数
				download: 0,
				// 读后感数量
				reaction: 0,
				// 提示框
				message: '',
				t_flag: false
			}
		},
		components: {
			'foot': f ,// 添加底部导航组件,
			"tips": tips
		},
		created: function(){
			var self=this;
			this.message='加载中';
			this.t_flag=true;
			var tid = self.util.getSessionData("userName");
			this.util.request(
			{
				url: self.globalurl.host+self.globalurl.url.grzx,
				data: {
					"loginAccount": self.util.getSessionData("userName")
				},
				dataType: 'json',
				type: 'POST'
			}, function(data){
				if(data.statusCode==200){
					self.score=data.score;
					self.download=data.download;
					self.schoolId=data.schoolId;
					self.name=data.name;
					self.avatar=data.avatar;
					self.reaction=data.reaction;
					self.schoolName=data.schoolName;
					self.tasks=data.tasks;
					self.t_flag=false;
				}
				if(data.statusCode==404){
					clearTimeout(timer);
					timer=null;
					self.message='暂无数据';
					timer=setTimeout(function(){
						self.t_flag=false;
					}, 2000);
				}
				if(data.statusCode==500){
					clearTimeout(timer);
					timer=null;
					self.message='服务器开小差啦，请稍后再试';
					self.t_flag=true;
					timer=setTimeout(function(){
						self.t_flag=false;
					}, 2000);
				}
			}, self)
			// $.ajax({
			// 	url: self.globalurl.host+self.globalurl.url.grzx,
			// 	data: {
			// 		"loginAccount": self.util.getSessionData("tId")
			// 	},
			// 	dataType: 'json',
			// 	type: 'POST',
			// 	success: function(data){
			// 		if(data.statusCode==200){
			// 			self.score=data.score;
			// 			self.download=data.download;
			// 			self.schoolId=data.schoolId;
			// 			self.name=data.name;
			// 			self.avatar=data.avatar;
			// 			self.reaction=data.reaction;
			// 			self.schoolName=data.schoolName;
			// 			self.tasks=data.tasks;
			// 			self.t_flag=false;
			// 		}
			// 		if(data.statusCode==404){
			// 			clearTimeout(timer);
			// 			timer=null;
			// 			self.message='暂无数据';
			// 			timer=setTimeout(function(){
			// 				self.t_flag=false;
			// 			}, 2000);
			// 		}
			// 		if(data.statusCode==500){
			// 			clearTimeout(timer);
			// 			timer=null;
			// 			self.message='服务器开小差啦，请稍后再试';
			// 			self.t_flag=true;
			// 			timer=setTimeout(function(){
			// 				self.t_flag=false;
			// 			}, 2000);
			// 		}
			// 	}
			// })
		},
		methods: {
			_exit: function(){
				var self=this;
				var type = this.util.getAppScene();
				if(type == 'app'){
					window.js.logout();//APP调用原生Android JS方法
				}else{
					$.ajax({
						url: 'http://yqyd.qgjydd.com/yqyd/wx/unbind',
						dataType: 'json',
						type: 'POST',
						success: function(data){
							if(data.errcode==200){
								clearTimeout(timer);
								timer=null;
								self.message='解绑成功';
								self.t_flag=true;
								self.util.removeSessionData('userName');
								timer=setTimeout(function(){
									self.t_flag=false;
									window.location.href='http://sz.kiway.cn/wxht/wechat/oauth.do?content=909c7d2e2777551de04813dfc15919d679c5cc734c1c7aebaf188b76c9cb0abef8e5013df9402c7c4536a0be59f7fc6f';
								}, 2000);
							}else{
								clearTimeout(timer);
								timer=null;
								self.message=data.errmsg;
								self.t_flag=true;
								timer=setTimeout(function(){
									self.t_flag=false;
								}, 2000);
							}
						}
					})
				}
			}
		}
	}
</script>

<style scoped>
	.top{
		margin-top: 0.88rem;
		width: 100%;
		background-image: url('../../static/images/pic_04.png');
		background-size: 100% 100%;
	}
	.info{
		overflow: hidden;
		width: 100%;
		padding: 0.35rem 0.45rem;
	}
	.img{
		float: left;
		width: 1.6rem;
		height: 1.6rem;
		border-radius: 100%;
		background-color: #fff;
		margin: 0 auto;
		padding: 0.055rem 0.055rem;
	}
	.img img{
		width: 1.5rem;
		height: 1.5rem;
		border-radius: 100%;
	}
	.name{
		padding-left: 2rem;
		margin-top: 0.4rem;
		font-size: 0.35rem;
		color: #fff;
		font-weight: 500;
	}
	.school{
		padding-left: 2rem;
		margin-top: 0.2rem;
		font-size: 0.33rem;
		color: #fff;
	}
	.teachinfo{
		margin-top: 0.35rem;
		display: flex;
		flex-flow: row;
		width: 100%;
		height: 1rem;
	}
	.t_i{
		position: relative;
		flex: 1;
	}
	.t_i p:nth-child(1){
		margin-top: 0.1rem;
		font-size: 0.33rem;
		text-align: center;
		color: #fff;
	}
	.t_i:nth-child(1):after{
		content: '';
		position: absolute;
		right: 0px;
		top: 50%;
		margin-top: -0.3rem;
		width: 0.01rem;
		height: 0.6rem;
		border-right: 0.01rem solid #e7e7e7;
	}
	.t_i:nth-child(2):after{
		content: '';
		position: absolute;
		right: 0px;
		top: 50%;
		margin-top: -0.3rem;
		width: 0.01rem;
		height: 0.6rem;
		border-right: 0.01rem solid #e7e7e7;
	}
	.t_i p:nth-child(2){
		margin-top: 0.15rem;
		font-size: 0.33rem;
		text-align: center;
		color: #fff;
	}
	/*签到*/
	/*.qd{
		flex: 1;
	}
	.qd a{
		display: block;
		margin-left: 0.6rem;
		width: 2.45rem;
		height: 0.8rem;
		background-image: url('../../static/images/ico_sign.png');
		background-size: 100% 100%;
		border-radius: 0.01rem;
	}*/
	.main{
		width: 100%;
		/*margin-bottom: 1.3rem;*/
	}
	.main a{
		line-height: 0.34rem;
		display: block;
		width: 100%;
		padding: 0.44rem 0.63rem 0.44rem 0.28rem;
		background-color: #fff;
		font-size: 0.3rem;
		color: #333333;
		border-bottom: 0.01rem solid #e7e7e7;
		background-image: url('../../static/images/ico_05.png');
		background-size: 0.15rem 0.27rem;
		background-repeat: no-repeat;
		background-position: 7.05rem center;
	}
	.groud{
		float: left;
		margin-right: 0.3rem;
		width: 0.33rem;
		height: 0.33rem;
		background-size: 100% 100%;
	}
	.main a:nth-of-type(2),.main a:nth-of-type(6){
		margin-top: 0.2rem;
		border-top: 0.01rem solid #e7e7e7;
	}
	.main a:nth-of-type(1) .groud{
		width: 0.35rem;
		height: 0.28rem;
		background-image: url('../../static/images/wd_xx.png');
	}
	.main a:nth-of-type(2) .groud{
		background-image: url('../../static/images/wd_06.png');
	}
	.main a:nth-of-type(3) .groud{
		background-image: url('../../static/images/wd_07.png');
	}
	.main a:nth-of-type(4) .groud{
		background-image: url('../../static/images/wd_08.png');
	}
	.main a:nth-of-type(5) .groud{
		background-image: url('../../static/images/wd_019.png');
	}
	.main a:nth-of-type(6) .groud{
		background-image: url('../../static/images/wd_sz.png');
	}
	.count{
		float: right;
		color: #666666;
	}
	.zore{
		color: #cccccc;
	}
	.quit{
		display: block;
		width: 85%;
		height: 1rem;
		line-height: 0.8rem;
		border-radius: 1rem;
		background-color: #40cc8b;
		color: #fff;
		font-size: 0.33rem;
		text-align: center;
		margin: 0 auto;
		margin-bottom: 1.3rem;
		margin-top: 0.2rem;
		border: 0px;
	}
</style>


