<template>
	<div>
		<header>
			<router-link to='/kstfb' class="goback"></router-link>
			<p>发布作业</p>
		</header>
		<div class="fb_cnt">
			<div class="t_info">
				<p>作业名称</p>
				<input type="text" name="" placeholder="请输入作业名称">
				<p>设置时间</p>
				<div class="fb_time">
					<input v-binit type="text" readonly="readonly" id="t_begin">
					<span>-</span>
					<input v-einit type="text" readonly="readonly" id="t_end">
				</div>
				<p>班级</p>
				<div class="fb_c_l">
					<div class="fb_cl">
						<div v-for='(c, i) in c_list'>
							<input :id="'aa'+i" class="radio" type="radio" name="class">
							<label :for="'aa'+i">
								<div class="fb_class" :class='{yes: cl_flag==i}' @click='_checked(i)'>{{c.name}}</div>
							</label>
						</div>
					</div>
					<div class="fb_icon"></div>
				</div>
			</div>
		</div>
		<div class="fbzy">
			<button class='fz' @click='_send' v-goto>发布作业</button>
			<router-link to='/kstfb'></router-link>
		</div>
		<tips :msg='message' v-show='flag'></tips>
	</div>
</template>

<script>
	import tips from "./msgtips.vue"
	export default {
		data(){
			return {
				// 提示框
				flag: false,
				message: '提交成功',
				// 班级
				cl_flag: -1,
				// 班级列表
				c_list: [
					{name: '2年3班'},
					{name: '2年3班'},
					{name: '2年3班'},
					{name: '2年3班'},
					{name: '2年3班'},
					{name: '2年3班'},
					{name: '2年3班'}
				],
				// 考核列表
				a_list: [
					{name: '做评测'},
					{name: '做评测'},
					{name: '做评测'},
					{name: '做评测'},
					{name: '做评测'},
					{name: '做评测'}
				]
			}
		},
		components: {
			"tips": tips
		},
		directives: {
			binit: {
				inserted: function(el){
					var calendar = new lCalendar();
					calendar.init({
					    'trigger': '#t_begin',
					    'type': 'date'
					});
				}
			},
			einit: {
				inserted: function(el){
					var calendar = new lCalendar();
					calendar.init({
					    'trigger': '#t_end',
					    'type': 'date'
					});
				}
			},
			goto: {
				inserted: function(el){
					el.onclick=function(){
						setTimeout(function(){
							el.parentNode.getElementsByTagName("a")[0].click();
						}, 2100);
					}
				}
			}
		},
		methods: {
			_checked: function(i){
				this.cl_flag=i;
			},
			_send: function(){
				this.flag=true;
				var self=this;
				setTimeout(function(){
					self.flag=false;
				}, 2000);
			}
		}
	}
</script>

<style scoped>
	.fb_cnt{
		position: absolute;
		top: 0.88rem;
		bottom: 0px;
		width: 100%;
		overflow: auto;
		background-color: #fff;
	}
	.t_info,.a_info{
		width: 100%;
		padding: 0.4rem 0.3rem;
		background-color: #fff;
	}
	.t_info p,.a_info p{
		font-size: 0.29rem;
		color: #808080;
	}
	.t_info p:nth-child(3),
	.a_info p:nth-child(3),
	.a_info p:nth-child(5),
	.t_info p:nth-child(5){
		margin-top: 0.55rem;
	}
	.t_info input{
		width: 100%;
		height: 1.05rem;
		border: 0px;
		border-bottom: 0.02rem solid #efefef;
		font-size: 0.35rem;
	}
	.fb_time{
		margin-top: 0.23rem;
		width: 100%;
	}
	.fb_time input{
		padding-left: 0.25rem;
		width: 3.2rem;
		height: 0.8rem;
		border: 0.01rem solid #d9d9d9;
		border-radius: 0.1rem;
		font-size: 0.3rem;
		background-image: url('../../static/images/ico_04.png');
		background-size: 0.40rem 0.40rem;
		background-repeat: no-repeat;
		background-position: 2.6rem center;
	}
	.fb_time span{
		color: #d9d9d9;
		font-size: 0.3rem;
	}
	.fb_c_l{
		display: box;
		display: -webkit-box;
		margin-top: 0.27rem;
		width: 100%;
		height: 0.7rem;
	}
	.fb_cl{
		display: box;
		display: -webkit-box;
		overflow: auto;
		width: 6.31rem;
		height: 0.72rem;
	}
	.fb_cl .radio{
		display: none;
	}
	.fb_icon{
		border: 0px;
		background-color: #fff;
		width: 0.6rem;
		height: 0.7rem;
		background-image: url('../../static/images/ico_05.png');
		background-size: 0.15rem 0.27rem;
		background-position: center center;
		background-repeat: no-repeat;
	}
	.fb_class{
		width: 1.8rem;
		height: 0.7rem;
		border: 0.01rem solid #cecece;
		border-radius: 0.34rem;
		font-size: 0.35rem;
		color: #cecece;
		text-align: center;
		line-height: 0.7rem;
		margin-right: 0.3rem;
		margin-left: 0.01rem;
	}
	.yes{
		border: 0px;
		width: 1.8rem;
		height: 0.7rem;
		border-radius: 0.34rem;
		font-size: 0.35rem;
		text-align: center;
		line-height: 0.7rem;
		margin-right: 0.3rem;
		margin-left: 0.01rem;
		color: #fff;
		background-color: #40cc8b;
	}
	.checked{
		background-color: #40cc8b;
		color: #fff;
		border: 0px;
	}
	.a_info{
		margin-top: 0.2rem;
		border-top: 0.01rem solid #e6e6e6;
		border-bottom: 0.01rem solid #e6e6e6;
	}
	.a_mth{
		overflow: auto;
		overflow-y: hidden;
		display: box;
		display: -webkit-box;
		width: 100%;
		height: 0.54rem;
		margin-top: 0.33rem;
	}
	.a_c{
		-webkit-appearance: none;
		background-image: url('../../static/images/Radio_no.png');
		background-size: 100% 100%;
		width: 0.54rem;
		height: 0.54rem;
		border: 0px;
	}
	.a_name{
		display: inline-block;
		height: 0.54rem;
		line-height: 0.54rem;
		font-size: 0.31rem;
		vertical-align: top;
		color: #b7b7b7;
		margin-right: 0.8rem;
	}
	.fbzy{
		position: fixed;
		bottom: 0px;
		left: 0px;
		width: 100%;
		height: 1rem;
	}
	.fbzy a{
		display: none;
	}
	.fz{
		width: 100%;
		height: 100%;
		text-align: center;
		line-height: 1rem;
		border: 0px;
		color: #fff;
		font-size: 0.39rem;
		background-color: #40cc8b;
	}
</style>


