<template>
	<div>
		<header>
			<router-link to='/' class="goback"></router-link>
			<p class="h_tit">作业列表</p>
			<select class="s_class">
				<option>2年3班</option>
				<option>2年3班</option>
				<option>2年3班</option>
			</select>
		</header>
		<div class="title">
			<router-link to="/kstfb" class="yfb">已发布(15)</router-link>
			<div class="wfb">
				<div>
					<p>未发布(25)</p>
				</div>
			</div>
		</div>
		<div class="fbzy">
			<router-link to="/kstfb/xt" class='fz'>布置作业</router-link>
		</div>
		<div class="cover" :style="show">
			<div class="tips">
				<p>是否删除该条作业</p>
				<div class="btn">
					<button @click="_y_dle">删除</button>
					<button @click="_n_dle">取消</button>
				</div>
			</div>
		</div>
		<div class="list">
			<div class="info" v-for="(w, i) in w_list">
				<router-link to="/kstfb/wxq" class="xq">
					<div class="top">
						<router-link to="/kstfb/wxq" class="topic">{{w.count}}&nbsp题</router-link>
						<p class="time">{{w.time}}</p>
						<p class="t_tle">{{w.title}}</p>
					</div>
				</router-link>
				<div class="bottom">
					<button class="delete" @click="_dle(i)"></button>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		data(){
			return {
				flag: false, // 提示框
				index: 0, // 作业索引
				w_list: [
					{title: '阅读理解', count: 5, time: '2017-01-01'},
					{title: '阅读理解1', count: 5, time: '2017-01-01'},
					{title: '阅读理解2', count: 5, time: '2017-01-01'},
					{title: '阅读理解3', count: 5, time: '2017-01-01'},
					{title: '阅读理解4', count: 5, time: '2017-01-01'},
					{title: '阅读理解5', count: 5, time: '2017-01-01'},
					{title: '阅读理解6', count: 5, time: '2017-01-01'}
				]
			}
		},
		methods: {
			_dle: function(i){
				this.flag=true;
				var self=this;
				document.addEventListener("touchmove", function(event){
					if(self.flag){
						event.preventDefault();
					}
				}, { passive: false });
				this.index=i;
				console.log(i);
				console.log(this.flag);
				console.log(this.index);
			},
			// 确定删除
			_y_dle: function(){
				// 删除指定数据
				this.w_list.splice(this.index,1);
				// 隐藏提示框
				this.flag=false;
			},
			// 不删除
			_n_dle: function(){
				// 隐藏提示框
				this.flag=false;
			}
		},
		computed: {
			show: function(){
				if(this.flag){
					return {
						display: 'block'
					}
				}else{
					return {
						display: 'none'
					}
				}
			}
		}
	}
</script>

<style scoped>
	.h_tit{
		float: left;
		height: 100%;
		margin-left: 0.75rem;
	}
	.s_class{
		float: right;
		margin-right: 0.3rem;
		padding: 0px 0.36rem;
		font-size: 0.3rem;
		color: #fff;
		width: 1.8rem;
		height: 100%;
		border: 0px;
		background-color: rgba(255,255,255,0);
		background-image: url('../../static/images/ico_06.png');
		background-position: center right;
		background-repeat: no-repeat;
		background-size: 0.22rem 0.11rem;
	}
	.title{
		position: fixed;
		width: 100%;
		display: flex;
		flex-flow: row;
		background-color: #fff;
		top: 0.88rem;
		border-bottom: 0.01rem solid #e7e7e7;
	}
	.wfb{
		position: relative;
		flex: 1;
	}
	.wfb div{
		width: 100%;
		height: 100%;
		padding: 0px 0.9rem;
	}
	.wfb div:before{
		content: "";
		position: absolute;
		top: 50%;
		left: 0px;
		color: #e9e9e9;
		width: 0.01rem;
		height: 0.45rem;
		border-left: 0.01rem solid #e7e7e7;
		margin-top: -0.225rem;
		margin-left: -0.005rem;
	}
	.wfb p{
		height: 0.9rem;
		line-height: 0.9rem;
		text-align: center;
		color: #40cc8b;
		font-size: 0.35rem;
		border-bottom: 0.04rem solid #40cc8b;
	}
	.yfb{
		flex: 1;
		font-size: 0.35rem;
		color: #808080;
		text-align: center;
		line-height: 0.9rem;
	}
	.list:nth-child(5){
		margin-top: 1.8rem;
	}
	.list{
		width: 100%;
		padding: 0.3rem;
		padding-bottom: 0px;
		margin-bottom: 0.28rem;
		height: 100%;
		overflow: auto;
	}
	.list:last-child{
		margin-bottom: 1rem;
	}
	.info{
		width: 100%;
		border: 0.01rem solid #e7e7e7;
		margin-bottom: 0.28rem;
		background-color: #fff;
		border-radius: 0.1rem;
	}
	.xq{
		width: 100%;
	}
	.top{
		width: 100%;
		padding: 0.34rem 0.3rem;
		border-bottom: 0.01rem solid #e7e7e7;
	}
	.time{
		color: #808080;
		font-size: 0.32rem;
	}
	.t_tle{
		margin-top: 0.15rem;
		font-size: 0.35rem;
		color: #333333;
	}
	.topic{
		display: block;
		width: 0.85rem;
		height: 0.82rem;
		float: right;
		font-size: 0.35rem;
		color: #41cd8c;
		line-height: 0.82rem;
		background-image: url('../../static/images/pic_more.png');
		background-repeat: no-repeat;
		background-size: 0.11rem 0.17rem;
		background-position: right center;
	}
	.bottom{
		overflow: hidden;
		width: 100%;
		padding: 0.11rem 0.3rem;
	}
	.delete{
		float: right;
		border: 0px;
		background-color: #fff;
		width: 0.4rem;
		height: 0.4rem;
		background-image: url('../../static/images/pic_del.png');
		background-size: 100% 100%;
	}
	.fbzy{
		position: fixed;
		bottom: 0px;
		left: 0px;
		z-index: 9999999;
		width: 100%;
		height: 1rem;
		background-color: #40cc8b;
	}
	.fz{
		display: block;
		width: 100%;
		height: 100%;
		text-align: center;
		line-height: 1rem;
		color: #fff;
		font-size: 0.39rem;
	}
	.cover{
		display: none;
		position: fixed;
		top: 0px;
		bottom: 0px;
		width: 100%;
		background-color: rgba(0,0,0,0.8);
		z-index: 9999999;
	}
	.tips{
		z-index: 99999999;
		position: absolute;
		top: 50%;
		left: 50%;
		width: 5.4rem;
		height: 3rem;
		margin-top: -1.5rem;
		margin-left: -2.7rem;
		border-radius: 0.1rem;
		background-color: rgb(255,255,255);
		border: 0.02rem solid #e7e7e7;
	}
	.tips p{
		width: 100%;
		height: 2.12rem;
		padding: 0.5rem;
		font-size: 0.35rem;
		line-height: 1.12rem;
		text-align: center;
		border-bottom: 0.01rem solid #e7e7e7;
	}
	.btn{
		display: flex;
		width: 100%;
		height: 0.88rem;
		overflow: hidden;
		border-bottom: 0.02rem solid #e7e7e7;
		border-radius: 0px 0px 0.1rem 0.1rem;
	}
	.btn button{
		flex: 1;
		border: 0px;
		border-right: 0.01rem solid #e7e7e7;
		text-align: center;
		line-height: 0.88rem;
		color: red;
		font-size: 0.3rem;
		background-color: #fff;
	}
	.btn button:nth-child(2){
		color: rgb(120,120,120);
		border: 0px;
	}
</style>


