<template>
	<div>
		<header>
			<router-link to='/kstfb/wfb' class="goback"></router-link>
			<p class="h_tit">已选题目</p>
			<router-link to='/' class="send"></router-link>
		</header>
		<div class="footer">
			<router-link to="/kstfb/wfb" class="save">保存，稍后修改</router-link>
			<router-link to="/kstfb/fb" class="issue">去发布(3)</router-link>
		</div>
		<div class="rubric" v-for='(t, i) in t_list'>
			<span v-show='t.option'>(单选题)</span>
			<p class="r_tle">{{t.title}}<span v-show='t.option'>（&nbsp;&nbsp;&nbsp;）。</span></p>
			<p v-show='t.option' class="option">A.{{t.o_1}}</p>
			<p v-show='t.option' class="option">B.{{t.o_2}}</p>
			<p v-show='t.option' class="option">C.{{t.o_3}}</p>
			<p v-show='t.option' class="option">D.{{t.o_4}}</p>
			<div class="tips">
				<p class="count">共{{t.count}}小题</p>
				<p class="d_o_d">平均难度{{t.diff}}</p>
				<p class="state">{{t.state}}</p>
				<button class="delete" @click='_del(i)'></button>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		data(){
			return {
				// 题目列表
				t_list: [
					{title: '每天早上醒来，你是否有给朋友道一句温暖的早安呢？以下是学习啦小编收集整理的早安带字图片，希望对…', count: 1, diff: 3.2, state: '布置过',  option: false, o_1: '', o_2: '', o_3: '', o_4: ''},
					{title: '下列语句朗读节奏停顿完全正确的一项是', count: 1, diff: 3.5, state: '布置过',  option: true, o_1: '选项一', o_2: '选项二', o_3: '选项三', o_4: '选项四'},
					{title: '下列语句朗读节奏停顿完全正确的一项是', count: 1, diff: 3.2, state: '布置过',  option: true, o_1: '选项一', o_2: '选项二', o_3: '选项三', o_4: '选项四'},
					{title: '下列语句朗读节奏停顿完全正确的一项是', count: 1, diff: 3.2, state: '布置过',  option: true, o_1: '选项一', o_2: '选项二', o_3: '选项三', o_4: '选项四'},
					{title: '下列语句朗读节奏停顿完全正确的一项是', count: 1, diff: 3.2, state: '布置过',  option: true, o_1: '选项一', o_2: '选项二', o_3: '选项三', o_4: '选项四'},
					{title: '下列语句朗读节奏停顿完全正确的一项是', count: 1, diff: 3.2, state: '布置过',  option: true, o_1: '选项一', o_2: '选项二', o_3: '选项三', o_4: '选项四'}
				]
			}
		},
		methods: {
			_del: function(i){
				this.t_list.splice(i, 1);
			}
		}
	}
</script>

<style scoped>
	.h_tit{
		float: left;
		height: 100%;
		margin-left: 0.75rem;
	}
	.send{
		float: right;
		display: block;
		width: 0.42rem;
		height: 0.42rem;
		margin-top: 0.22rem;
		margin-right: 0.27rem;
		background-image: url('../../static/images/ico_09fff.png');
		background-size: 100% 100%;
	}
	.add{
		float: right;
		display: block;
		width: 0.42rem;
		height: 0.42rem;
		margin-top: 0.22rem;
		margin-right: 0.65rem;
		background-image: url('../../static/images/ico_24fff.png');
		background-size: 100% 100%;
	}
	.title{
		background-color: #fff;
		margin-top: 0.88rem;
		width: 100%;
		padding: 0.39rem 0.37rem;
		border-bottom: 0.01rem solid #e7e7e7;
	}
	.t_cnt{
		width: 100%;
		font-size: 0.28rem;
		color: #666666;
		line-height: 0.4rem;
	}
	.tips{
		width: 100%;
		height: 0.5rem;
		margin-top: 0.3rem;
	}
	.delete{
		float: right;
		margin-top: 0.1rem;
		width: 0.35rem;
		height: 0.35rem;
		background-color: #fff;
		border: 0px;
		background-image: url('../../static/images/pic_del.png');
		background-size: 100% 100%;
	}
	.tips p{
		float: left;
		padding: 0px 0.17rem;
		height: 100%;
		line-height: 0.5rem;
		color: #fff;
		font-size: 0.32rem;
		border-radius: 0.1rem;
		margin-right: 0.15rem;
	}
	.count{
		background-color: #79a8ec;
	}
	.d_o_d{
		background-color: #f66c6c;
	}
	.state{
		background-color: #40cc8b;
	}
	.rubric{
		margin-top: 0.2rem;
		width: 100%;
		border-top: 0.01rem solid #e7e7e7;
		padding: 0.35rem;
		background-color: #fff;
	}
	.rubric:nth-child(3){
		margin-top: 0.8rem;
	}
	.rubric:last-child{
		margin-bottom: 1.01rem;
	}
	.r_tle{
		width: 100%;
		font-size: 0.35rem;
		color: #333333;
		line-height: 0.5rem;
	}
	.rubric span{
		float: left;
		color: #40cc8b;
		font-size: 0.35rem;
		line-height: 0.4rem;
		margin-right: 0.65rem;
	}
	.r_tle span{
		float: none;
		color: #333333;
	}
	.option{
		width: 100%;
		color: #808080;
		font-size: 0.33rem;
		line-height: 0.4rem;
		margin-top: 0.2rem;
	}
	.footer{
		position: fixed;
		bottom: 0px;
		left: 0px;
		z-index: 999;
		background-color: #fff;
		width: 100%;
		height: 1.01rem;
		display: flex;
		flex-flow: row;
		border-top: 0.01rem solid #e7e7e7;
	}
	.save{
		flex: 1;
		background-color: #fff;
		color: #808080;
		font-size: 0.35rem;
		line-height: 1rem;
		text-align: center;
	}
	.issue{
		flex: 1;
		color: #fff;
		font-size: 0.35rem;
		line-height: 1rem;
		text-align: center;
		background-color: #40cc8b;
	}
</style>


