<template>
	<div>
		<header>
			<router-link to='/sc' class="goback"></router-link>
			<p>小巴掌故事</p>
		</header>
		<div class="state">
			<p class="count"><span>{{topic.id}}</span>/{{length}}</p>
		</div>
		<div class="content">
			<p class="title">单选题</p>
			<p class="diff">(难度系数：<span></span>)</p>
			<p class="topic">{{topic.id}}.{{topic.title}}(&nbsp;&nbsp;&nbsp;&nbsp;)</p>
			<a href="javascript:void(0)">{{topic.option_1}}</a>
			<a href="javascript:void(0)">{{topic.option_2}}</a>
			<a href="javascript:void(0)">{{topic.option_3}}</a>
			<a href="javascript:void(0)">{{topic.option_4}}</a>
		</div>
		<footer>
			<button @click='_last(topic.id)'>上一题</button>
			<button @click='_next(topic.id, length)'>下一题</button>
		</footer>
	</div>
</template>

<script>
	export default {
		data(){
			return {
				length: 0,
				topic: {},
				t_list: [
					{id: 1,title: '请问下面哪一项是正确',option_1: 'A.选项一',option_2: 'B.选项二',option_3: 'C.选项三',option_4: 'D.选项四'},
					{id: 2,title: '1954年重大事件',option_1: 'A.123',option_2: 'B.321',option_3: 'C.131',option_4: 'D.212'},
					{id: 3,title: '秦国哪一年建立',option_1: 'A.111',option_2: 'B.222',option_3: 'C.333',option_4: 'D.000'}
				]
			}
		},
		created: function(){
			this.topic=this.t_list[0];
			this.length=this.t_list.length;
		},
		methods: {
			_last: function(i){
				if(i!=1){
					this.topic=this.t_list[i-2];
				}
			},
			_next: function(i, l){
				if(i!=l){
					this.topic=this.t_list[i];
				}
			}
		}
	}
</script>

<style scoped>
	.state{
		border-bottom: 0.01rem solid #e7e7e7;
		background-color: #fff;
		width: 100%;
		padding: 0.28rem;
		margin-top: 0.88rem;
		font-size: 0.32rem;
	}
	.section{
		color: #79a8ec;
		float: left;
	}
	.count{
		width: 50%;
		margin: 0 auto;
		color: #9c9c9c;
		text-align: center;
	}
	.count span{
		color: #40cc8b;
	}
	.grade{
		float: right;
		color: #f66c6c;
	}
	.content{
		position: absolute;
		top: 1.77rem;
		left: 0px;
		bottom: 1.01rem;
		width: 100%;
		padding: 0.38rem 0.27rem;
		background-color: #fff;
	}
	.title{
		font-size: 0.4rem;
		color: #333333;
		float: left;
	}
	.diff{
		float: left;
		line-height: 0.36rem;
		font-size: 0.32rem;
		color: #999999;
		margin-left: 0.32rem;
		vertical-align: text-bottom;
	}
	.diff span{
		display: inline-block;
		width: 0.29rem;
		height: 0.29rem;
		background-image: url('../../static/images/xs_ico_25.png');
		background-size: 100% 100%;
		vertical-align: top;
	}
	.topic{
		width: 100%;
		font-size: 0.35rem;
		color: #333333;
		margin-top: 0.95rem;
	}
	.content a{
		margin-top: 0.25rem;
		display: block;
		font-size: 0.33rem;
		color: #808080;
		line-height: 0.5rem;
	}
	a.error{
		color: red;
	}
	a.yes{
		color: #40cc8b;
	}
	footer{
		display: flex;
		flex-flow: row;
		position: fixed;
		bottom: 0px;
		left: 0px;
		width: 100%;
		height: 1.01rem;
		background-color: #fff;
		border-top: 0.01rem solid #e7e7e7;
	}
	footer button{
		flex: 1;
		line-height: 1rem;
		text-align: center;
		font-size: 0.32rem;
		color: #808080;
		border: 0px;
		background-color: #fff;
	}
	footer button:last-child{
		background-color: #79a8ec;
		color: #fff;
	}
</style>


