<template>
	<div id="ydzx" style="font-family: 黑体; ">
		<header>
			<router-link to='/' class="goback"></router-link>
			<p>阅读资讯</p>
		</header>
		<div class="ydzx_main" >
			<router-link :to='"/ydzx/xq?id="+ydzx.id'  v-for="ydzx in ydzx_list" :key='ydzx.id'>
				<div class="ydzx_list">
					<div class="ydzx_l">
						<div class="ydzx_titil" >{{ydzx.title}}</div>
						<div class="ydzx_time">{{ydzx.createDate}}</div>
					</div>
					<img :src="ydzx.image" class="ydzx_img" v-if="ydzx.image"></img>
					<div class="ydzx_text">{{ydzx.shortContent}}</div>
					<div class="ydzx_f">
						<div>阅读全文</div>
						<img src="../../static/images/pic_more.png" class="ydzx_more"></img>
					</div>
				</div>
			</router-link>
		</div>
		<tips :msg='message' v-show='flag'></tips>
	</div>
</template>
<script>
	var timer=null;
	import tips from './msgtips.vue'
	export default {
		data(){
			return {
				// 阅读资讯列表
				ydzx_list: [],
				// 页数
				page: 1,
				// 提示框
				flag: false,
				message: '',
				nextpage: true
			}
		},
		name: 'ydzx',
		created: function(){
			var self=this;
			this.message='加载中';
			this.flag=true;
			// --------------请求资讯列表信息begin--------------
			this.util.request(
			{
				url: self.globalurl.host+self.globalurl.url.ydzx,
				data: {"page":"1", "pageSize": '3'},
				dataType: 'json',
				type: 'POST'
			}, function(data){
				if(data.status==200&&data.result.length!=0){
					for(let i=0;i<data.result.length;i++){
						self.ydzx_list.push(data.result[i]);
						self.flag=false;
					}
				}
			}, self)
			// $.ajax({
			// 	url: self.globalurl.host+self.globalurl.url.ydzx,
			// 	data: {"token":"11111","page":"1", "pageSize": '3'},
			// 	dataType: 'json',
			// 	type: 'POST',
			// 	success: function(data){
			// 		if(data.status==200&&data.result.length!=0){
			// 			for(let i=0;i<data.result.length;i++){
			// 				self.ydzx_list.push(data.result[i]);
			// 				self.flag=false;
			// 			}
			// 		}
			// 	}
			// })
			// --------------请求资讯列表信息end--------------
			// 上拉加载
			window.addEventListener('scroll', function(){
				if(document.body.scrollTop+document.documentElement.clientHeight==document.body.scrollHeight){
					// 只在查看班级页面有效
					if(self.$route.path=='/ydzx'){
						if(self.nextpage){
							self.page+=1;
							self.message='加载中';
							self.flag=true;
							self.util.request(
							{
								url: self.globalurl.host+self.globalurl.url.ydzx,
								data: {"page":self.page, "pageSize": '3'},
								dataType: 'json',
								type: 'POST'
							}, function(data){
								if(data.status==200&&data.result.length!=0){
									self.nextpage=data.nextPage;
									for(let i=0;i<data.result.length;i++){
										self.ydzx_list.push(data.result[i]);
									}
									self.flag=false;
								}
								// if(data.status==404){
								// 	clearTimeout(timer);
								// 	timer=null;
								// 	self.message='暂无数据';
								// 	self.show=true;
								// 	timer=setTimeout(function(){
								// 		self.show=false;
								// 	}, 2000);
								// }
							}, self)
						}else{
							self.message='数据已到底';
							self.show=true;
							timer=setTimeout(function(){
								self.show=false;
							}, 2000);
						}
						// $.ajax({
						// 	url: self.globalurl.host+self.globalurl.url.ydzx,
						// 	data: {"token":"11111","page":self.page, "pageSize": '3'},
						// 	dataType: 'json',
						// 	type: 'POST',
						// 	success: function(data){
						// 		if(data.status==200&&data.result.length!=0){
						// 			for(let i=0;i<data.result.length;i++){
						// 				self.ydzx_list.push(data.result[i]);
						// 			}
						// 			self.flag=false;
						// 		}
						// 		if(data.status==404){
						// 			clearTimeout(timer);
						// 			timer=null;
						// 			self.message='暂无数据';
						// 			self.show=true;
						// 			timer=setTimeout(function(){
						// 				self.show=false;
						// 			}, 2000);
						// 		}
						// 	}
						// })
					}
				}
			})
		},
		components: {
			'tips': tips
		}
	}
</script>
<style scoped>
	/*头部*/
	header{
		position: fixed;
		z-index: 1;
		top: 0px;
		left: 0px;
		width: 100%;
		height: 0.88rem;
		background-color: #40cc8b;
		text-align: center;
	}
	header p{
		font-size: 0.36rem;
		line-height: 0.88rem;
		color: #fff;
	}
	.goback{
		position: absolute;
		top: 50%;
		left: 0.25rem;
		margin-top: -0.185rem;
		display: inline-block;
		width: 0.37rem;
		height: 0.37rem;
		background-image: url('../../static/images/pic_fh.png');
		background-size: 100% 100%;
	}
	.ydzx_main{
		width: 100%; 
		position: relative;
		top:0.88rem;
	}
	.ydzx_list{
		margin:0.2rem 0.3rem;
		border:#e8e8e8 1px solid;
		border-radius: 0.1rem;
		background-color: #fff;
	}
	.ydzx_l{
		padding: 0.2rem;
	}
	.ydzx_img{
		/*display: inline-block;*/
		width: 6.3rem;
		height: 3.6rem;
		margin:0.06rem 0.3rem;
		background-size: 100% 100%;
	}
	.ydzx_text{
		font-size: 0.32rem;
		color: #999;
		padding: 0.1rem 0.3rem ;
		line-height: 0.45rem; 
		background-color: #fff;
	}
	.ydzx_more{
		position: absolute;
		top: 50%;
		right: 0.25rem;
		margin-top: -0.15rem;
		display: inline-block;
		width: 0.18rem;
		height: 0.30rem;
		background-size: 100% 100%;
	}
	.ydzx_titil{
		color:#333; 
		font-size:0.36rem; 
		line-height: 0.6rem; 
		margin-right: 0.7rem;
		overflow:hidden; 
		text-overflow:ellipsis; 
		white-space:nowrap;
	}
	.ydzx_time{
		color:#b7b7b7; 
		font-size: 0.34rem;
	}
	.ydzx_f{
		height: 0.85rem; 
		position: relative; 
		border-top:#e8e8e8 1px solid; 
		line-height: 1rem;
		padding-left: 0.3rem; 
		color: #4d4d4d; 
		font-size: 0.34rem; 
		line-height: 0.85rem;
	}
</style>


